@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

.general {
  /* background-color: #ffffff; */
  background: repeating-linear-gradient(
    90deg,
    #ff98a0, #ff98a0 16.67%,
    #ffc080 16.67%, #ffc080 33.33%,
    #ffe8c0 33.33%, #ffe8c0 50%,
    #d8f0c0 50%, #d8f0c0 66.67%,
    #b8e8d8 66.67%, #b8e8d8 83.33%,
    #c0d0f0 83.33%, #c0d0f0 100%
  );
  align-items: center;
  justify-content: center;
  display: flex;
  font-size: 16px;
  font-family: 'Inter';
  font-weight: 400;
  height: 100vh;
}

.left-column {
  position: absolute;
  right: 55%;
  margin: 4px 10px 0px 0px;
  font-weight: 700;
}

.right-column {
  position: absolute;
  left: 45%;
  margin: 0px 0px 0px 0px
}

.row {
  margin: 15px 0px 15px 0px;
}

.resultPage {
  height: unset !important;
}

.resultField {
  margin: 0px 0px 25px 0px;
}

#nameField h2 {
  display: inline;
}

#nameField p {
  display: inline;
}

#classField p {
  display: inline;
}

h1 {
  position: relative;
  font-weight: 1000;
  text-align: center;
}

input {
  font-size: 16px;
  font-family: 'Inter';
  font-weight: 400;
  width: 300px;
  border-style: solid;
  border-radius: 8px;
  border-width: 2px;
  padding: 2px 4px 2px 4px;
}

select {
  font-size: 16px;
  font-family: 'Inter';
  font-weight: 400;
  width: 312px;
  border-style: solid;
  border-radius: 8px;
  border-width: 2px;
  padding: 2px 0px 2px 0px;
}

button {
  font-size: 16px;
  font-family: 'Inter';
  font-weight: 700;
  width: 100px;
  border-style: solid;
  border-radius: 8px;
  border-width: 2px;
  margin: 10px 0px 0px 0px;
  padding: 2px 0px 2px 0px;
}

button:hover{
  background-color:red;
}

.backgroundRed {
  position: absolute;
  left: 0%;
  width: 16.7%;
  background-color: #ff98a0;
}